<template>
    <v-container>
        <v-row justify="center">
            <v-col cols="12" sm=8 md=6 lg=4>
                <v-form ref="form" v-model="valid">
                    <v-text-field
                        name="username"
                        v-model='username'
                        label="Felhasználónév"
                        autocomplete="username"
                        prepend-icon="mdi-account"
                        :rules="userRules"
                        id="uname"
                        required
                    ></v-text-field>    
                    <v-text-field
                        name="email"
                        v-model="email"
                        label="Email cím"
                        autocomplete="email"
                        prepend-icon="mdi-email"
                        id="email"
                        :rules="emailRules"
                        type="email"
                        required
                    ></v-text-field>                
                    <v-text-field
                        name="phone"
                        v-model="phone"
                        label="Telefonszám"
                        prepend-icon="mdi-phone"
                        id="phone"
                        :rules="phoneRules"
                        required
                    ></v-text-field>                

                    <v-btn color="orange" @click="submit">
                        Mentés
                    </v-btn>
                </v-form>
            </v-col>
            <v-snackbar v-model="snackbar" bottom :timeout="snackTimeout">
                Adatait elmentettük!
                <v-btn text color="blue" @click="snackbar = false">
                    Bezár
                </v-btn>
            </v-snackbar>

        </v-row>
    </v-container>
    
</template>

<script>

export default {
    data : () => {
        return {
            username : '',
            email : '',
            phone : '',
            snackbar: false,
            snackTimeout: 1250,
            valid: true,
            userRules: [
                v => !!v || 'Kötelező mező',
                v => v&&v.length >= 5 || 'A hossz minimum 5 karakter.'
            ],
            emailRules: [
                v => !!v || 'Kötelező mező',
                v => /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,10})+$/.test(v) || 'Kérem valós email címet adjon meg.'
            ],
            phoneRules: [
                v => !!v || 'Kötelező mező',
                v => v&&v.length >= 8 || 'A hossz minimum 8 karakter.'
            ],

        }
    },
    methods : {
        submit () {
            if (this.$refs.form.validate()) {
                this.valid=true;
                this.$store.commit('setUname',this.username);
                this.$store.commit('setUmail',this.email);
                this.$store.commit('setUphone',this.phone);          
                this.snackbar = true;
            } else {
                this.valid=false
            }
        }
    },
    mounted() {
        this.username = this.$store.state.uname;
        this.email = this.$store.state.umail;
        this.phone = this.$store.state.uphone;
    }    
}
</script>

