<script>
  export default {
    components: {
    },
    mounted() {
      if (this.$store.state.uname === '') {
        let uname = localStorage.getItem('uname');
        let umail = localStorage.getItem('umail');    
        let uphone = localStorage.getItem('uphone');    
        if ((uname === null)||(umail === null)||(uphone === null)) {
            null //   Do nothing
        } else {
          this.$store.commit('setUname',uname);
          this.$store.commit('setUmail',umail);
          this.$store.commit('setUphone',uphone);          
        }    
      }       
    }
  }
</script>

