import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    basketCounter : 0,
    basket : [],
    uname : '',
    umail : '',
    uphone : '' 
  },
  mutations: {
    resetBasket (state) {
      state.basketCounter = 0;
      state.basket = [];
    },
    addToBasket(state,item) {
      state.basketCounter++;
      state.basket.push(item);
    },
    delFromBasket(state,i) {
      state.basket.splice(i.i,1);
      state.basketCounter--;
    },
    setUname ( state, uname ) {
      localStorage.setItem('uname',uname)
      state.uname = uname;
    },
    setUmail ( state, umail ) {
      localStorage.setItem('umail',umail)
      state.umail = umail;
    },
    setUphone ( state, uphone ) {
      localStorage.setItem('uphone',uphone)
      state.uphone = uphone;
    },
  },
  actions: {
  },
  modules: {
  }
})
