<template>
  <div>
    <!-- Detail dialog -->
    <item-details v-if="dialog" @closeDialog="dialog=false" :item="detailedItem"></item-details>
    <!-- Add to basket snackbar -->
    <v-snackbar v-model="snackbar" :timeout="snackTimeout">
      Kosárban {{ $store.state.basketCounter }} tétel / {{sumPrice}} 
      <v-btn color="blue" text @click="snackbar = false">
        Bezár
      </v-btn>
    </v-snackbar>
    <!-- The list itself -->
    <v-treeview dense open-on-click  :items="items">
        <template slot="label" slot-scope="{ item }">
          <v-container fluid pa-0>
            <v-row justify="start" no-gutters>
              <v-col @click="showDetails(item)">
                <v-icon class="mx-2" v-if="!item.children">mdi-information-outline</v-icon>{{ item.name }}
              </v-col>
              <v-spacer></v-spacer>
              <v-col cols="3" sm="2" md="1" class="text-right" v-if="item.price"> {{ item.price}} </v-col>
              <v-col cols="2" class="text-center">
                <v-icon color="green" v-if="!item.children" @click="addToBasket(item)">mdi-cart-plus</v-icon>
              </v-col>
            </v-row>
          </v-container>
        </template>
     </v-treeview>
  </div>
</template>

<script>

import ItemDetails from '@/components/ItemDetails.vue'

export default {
  components: {
    ItemDetails
  },
  data: () => ({
      dialog : false,
      snackbar: false,
      imgLoading: true,
      snackTimeout: 1250,
      detailedItem : {},
      items: [
        {
          id: 1,
          name: 'Pizzák ',
          children: [
            { id: 2, name: 'Margarita', price: 1250, desc: 'Paradicsom, Mozzarella, Bazsalikom', pic: 'pizza_margeritha.jpg' },
            { id: 3, name: 'Négy évszak', price: 1410, desc: 'Paradicsom, Mozzarella, Sonka, Gomba, Olivabogyó', pic: 'negy-evszak-pizza.jpg' },
            { id: 4, name: 'Hawaii', price: 1450, desc: 'Paradicsom, Mozzarella, Sonka, Ananász',pic: 'hawaii-pizza.png' },
          ],
        },
        {
          id: 5,
          name: 'Italok ',
          children: [
            {
              id: 6,
              name: 'Sörök ',
              children: [
                { id: 7, name: 'Stella', price: 460, desc: 'Alkoholtartalom: 5%',pic: 'Stella_Artois.jpg'},
                { id: 8, name: 'Soproni', price: 400, desc: 'Alkoholtartalom: 4.7%',pic: 'soproni.jpg'},
                { id: 9, name: 'Borsodi',  price: 400, desc: 'Alkoholtartalom: 4.8%',pic: 'borsodi.jpg'},
              ],
            },
            {
              id: 10,
              name: 'Üdítők',
              children: [
                    { id: 12, name: 'Coca Cola', price: 390, desc: 'Szénsavas üdítőital, 30 kcal/ 100ml',pic: 'coca_05.jpg' },
                    { id: 13, name: 'Fanta', price: 390 , desc: 'Szénsavas üdítőital, 32 kcal/ 100ml',pic: 'fanta.jpg' },
                    { id: 14, name: 'Ice Tea', price: 390 , desc: 'Szénsavas üdítőital, 26 kcal/ 100ml',pic: 'icetea.jpg' },
                  ],
                },
              ],
            },
          ],
    }),
    computed: {
      picurl() {
        return 'img/'+this.detailedItem.pic;
      },
      sumPrice() {
        return Intl.NumberFormat('hu-HU', { style: 'currency', currency: 'HUF', minimumFractionDigits: 0 }).format(this.$store.state.basket.reduce((a,b) => a + b.price,0))
      }
    },
    methods: {
      addToBasket(item) {
        this.$store.commit('addToBasket',item)
        this.snackbar=true;
      },
      showDetails(item) {
        if (item.children) return;
        this.detailedItem = item;
        this.dialog = true;        
      }
    }
}
</script>
