<template>
  <div>
    <!-- Detail dialog -->
    <item-details v-if="itemDialog" @closeDialog="itemDialog=false" :item="detailedItem"></item-details>
    <!-- Actual Basket -->
    <v-container>
      <v-row>
        <v-col>
          <v-list dense class="pa-0">
            <v-list-item v-for="(item,i) in $store.state.basket" :key="i">
                <v-list-item-icon>
                  <v-icon class="mx-2" @click="showDetails(item)">mdi-information-outline</v-icon>
                </v-list-item-icon>
                <v-list-item-content class="pa-1" @click="showDetails(item)">                  
                  {{ item.name }}
                </v-list-item-content>  
                <v-spacer></v-spacer>                
                <v-list-item-content class="pa-1">                  
                  {{item.price}} Ft
                </v-list-item-content>                  
                <v-list-item-action class="ma-0">
                  <v-btn icon @click="deleteItem(i)"> 
                    <v-icon color="red">mdi-close-circle</v-icon>
                  </v-btn>
                </v-list-item-action>
            </v-list-item>
          </v-list>
        </v-col>
      </v-row>       
    </v-container>
    <v-footer color="warning" dark height="80">
      <v-row align="center">
        <v-col cols="1" sm="2"></v-col>
        <v-col cols="5" sm="4" md="5" >
           Összesen: {{sumPrice}} 
        </v-col>
        <v-col v-if="noUserData"cols="5" sm="4" md="3" align="center" >
            <v-btn @click="$router.push({ name : 'personal'})">Adataim </v-btn>
        </v-col>
        <v-col v-else cols="5" sm="3" md="2" align="center" >
            <v-btn @click="order()">Rendelés</v-btn>
        </v-col>

        <v-col cols="1" sm="2"></v-col>
      </v-row>
    </v-footer>
<!--  Order placed -->
     <v-dialog v-model="dialog" max-width="290" persistent>
      <v-card>
        <v-card-title class="headline">Sikeres Rendelés</v-card-title>
        <v-card-text>
          Köszönjük rendelését. Rendelésazonosító: <b>51214</b>. Amint elkészültünk vele, azonnal értesítjük. Addig is szíves türelmét kérjük.
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-2" text @click="afterDialog()">
            ok
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    
  </div>
</template>

<script>

import ItemDetails from '@/components/ItemDetails.vue'

export default {
  components:  {
    ItemDetails
  },
  data: () => ({
    dialog: false,
    itemDialog: false,
    detailedItem : {},
  }),
  computed: {
    sumPrice() {
      return Intl.NumberFormat('hu-HU', { style: 'currency', currency: 'HUF', minimumFractionDigits: 0 }).format(this.$store.state.basket.reduce((a,b) => a + b.price,0))
    },
    noUserData() {
      return this.$store.state.uname == '' 
    }
  },
  methods: {
    deleteItem(i) {
      this.$store.commit('delFromBasket',{i})
      console.log(i);      
    },
    order() {
      this.dialog=true;
    },
    afterDialog() {
      this.dialog=false;
      this.$store.commit('resetBasket');      
      this.$router.push({ name : 'welcome'})
    },
      showDetails(item) {
        this.detailedItem = item;
        this.itemDialog = true;        
      }
  },
};
</script>
