<template>
    <v-dialog class="ma-3" width="95%" value="true" @click:outside="closeDetails()">
      <v-container>
        <v-row align="center" justify="center">
          <v-card max-width="500" width="100%" @click="closeDetails()">
              <v-img class="white--text align-end" :src="picurl" @load="imgLoading=false">
                <v-card-title>{{item.name}}</v-card-title>
              </v-img>
               <v-row class="fill-height ma-0" align="center" justify="center" v-if="imgLoading">
                 <v-progress-circular indeterminate color="black"></v-progress-circular>
               </v-row>

              <v-card-text class="text--white">
                <div>{{item.desc}}</div>
              </v-card-text>

              <v-card-actions>
                <v-btn color="orange">
                  <v-icon @click="closeDetails()">mdi-close-circle-outline</v-icon>
                </v-btn>
              </v-card-actions>
            </v-card>        
          </v-row>
      </v-container>
    </v-dialog>
</template>

<script>
export default {
  data: () => ({
    imgLoading: true
  }),
  props : ['item'],
  computed: {
     picurl() {
        return 'img/'+this.item.pic;
      }
  },
  methods: {
      closeDetails() {
          this.imgLoading=true;
          this.$emit('closeDialog');
      }
  },
};
</script>


